export function showUser (id) {
  return Request.get(`user/detail`, { params: { id: id } })
}

export function editUser (id, formData) {
  return Request.promisePost('user/edit', formData, { params: { id: id } })
}

export function delUser (id) {
  return Request.post('user/delete', { userId: id })
}
export function lockUser (id) {
  return Request.post('user/lock', { userId: id })
}

export function createUser (formData) {
  return Request.promisePost('user/create', formData)
}

export function checkNickname (nickname) {
  return Request.get('user/checkNickname', {
    params: { nickname: nickname }
  })
}

export function checkEmail (email) {
  return Request.get('user/checkEmail', {
    params: { email: email }
  })
}

export function checkMobile (mobile, userId = null) {
  const query = { mobile: mobile }
  if (userId) {
    query.userId = userId
  }

  return Request.get('user/checkMobile', {
    params: query
  })
}
