import Request from '@/axios/request'

export function getVueRouters () {
  return Request.get('setting/vueRouters')
}

export function getValue (name) {
  return Request.get('setting/getValue', {
    params: { name: name }
  })
}

export function editValue (name, config) {
  return Request.post(`setting/editValue`, config, { params: { name: name } })
}

export function getIpBlacklist () {
  return Request.get('setting/getIpBlacklist')
}

export function editIpBlacklist (config) {
  return Request.post('setting/editIpBlackList', config)
}

export function getAkServer () {
  return Request.get('setting/getAkServer')
}

export function editAkServer (config) {
  return Request.post('setting/editAkServer', config)
}
