import Cookies from 'js-cookie'
const key = 'admin-logged-in'

export function loggedIn () {
  // let identity = localStorage.getItem(key)
  const identity = Cookies.get(key)

  return identity ? JSON.parse(identity) : null
}

export function setLoggedIn (token, user) {
  // return localStorage.setItem(key, JSON.stringify({
  //   token: token,
  //   userinfo: user
  // }))
  return Cookies.set(key, JSON.stringify({
    token: token,
    userinfo: user
  }), {
    // expires: 7,
    path: '/'
  })
}

export function removeLoggedIn () {
  // return localStorage.removeItem(key)
  return Cookies.remove(key)
}
