import Vue from 'vue'
import { getConfigsValueByCategorySlug } from '@/api/configs'
import { SETTING_SITE } from '@/libs/constants'
import _get from 'lodash/get'

export default {
  state: {},
  mutations: {
    SETTING_VALUE (state, { path, value }) {
      Vue.set(state, path, value)
    }
  },
  getters: {
    getValue: (state) => (path, defaultVal) => {
      return _get(state, path, defaultVal)
    }
  },
  actions: {
  }
}
